<!---->
<template>
    <td :class="tdClass" :width="column.width"
        @keyup.enter="handleEnter"
        @click="handleClick" @dblclick="handleDbClick">
        <div class="td_warp" tabindex="0" :style="tdWarpStyle"
             @focus="handleFocus"
             ref="tdWarp" @blur="handleBlur">
            <template v-if="match">
                <div v-if="debug" v-html="getState()+value"
                     style="color: red;font-size: xx-small;"></div>
                <div v-if="false" v-html="'h_w:'+headWidth+',b_w:'+bodyWidth"
                     style="color: red;font-size: xx-small;"></div>
                <LjsTdHead ref="tdHead" :td="getThis()"></LjsTdHead>
                <!---->
                <Render :style="inputStyle" v-if="this.column.render" ref="input" :driver="driver"
                        :draw="column.render" :data="data" :column="column">
                </Render>
                <template v-else>
                    <LjsTextArea :auto-size="this.column.autoLine"
                                 :auto-select="true"
                                 :one-line-height="this.tr.table.lineHeight"
                                 :disabled="this.column.edit===false"
                                 :style="textareaStyle"
                                 ref="input"
                                 @blur="handleBlur"
                                 @focus="handleFocus"
                                 v-model="value"
                                 @input="handleInput"/>
                </template>
            </template>
        </div>
    </td>
</template>
<script>
  import Render from './Render.js'
  import LjsTdHead from './LjsTdHead.vue'
  import LjsInput from './LjsInput.vue'
  import LjsTextArea from './LjsTextArea.vue'

  // 
  var States = {normal: 0, select: 1, lock: 2}
  export default {
    name: 'LjsEditTd',
    components: {Render, LjsTdHead, LjsInput, LjsTextArea},
    props: {
      trHeight: {
        type: Number,
        default: undefined
      },
      index: {
        type: Number
      },
      tr: {
        type: Object
      },
      data: {
        type: Object,
        default () {
          return {}
        }
      },
      column: {
        type: Object,
        default () {
          return {}
        }
      }
    },
    watch: {
      state () {
        switch (this.state) {
          case States.normal:
            break
          case States.select:
            break
          case States.lock:
            this.tr.table.canMove = false
            break
        }
      }
    },
    computed: {
      textareaStyle () {
        let textareaStyle = {width: this.bodyWidth + 'px'}
        return textareaStyle
      },
      tdWarpStyle () {
        let tdWarpStyle = {}
        return tdWarpStyle
      },
      inputStyle () {
        let re = {}
        re.width = this.bodyWidth + 'px'
        if (!this.column.autoLine) {
          re.height = this.table.lineHeight + 'px'
          re.overflow = 'hidden'
        }
        return re
      },
      debug: {get () { return this.table.debug }},
      driver: {get () { return this.table.driver }},
      table: {get () { return this.tr.table }},
      // x
      x: {get () { return this.index }},
      // y
      y: {get () { return this.tr.index }},
      // 
      bodyWidth: {
        get () {
          let bodyWidth = this.column.width - this.headWidth
          return bodyWidth
        }
      },
      headWidth: {
        get () {
          let headWidth = 0
          let tdHead = this.$refs.tdHead
          if (this.isMounted && tdHead) headWidth = tdHead.width
          else headWidth = 0
          return headWidth
        }
      },
      // 
      tdClass: {
        get () {
          let tdClass = ''
          switch (this.state) {
            case States.normal:
              tdClass = 'td_mormal'
              break
            case States.select:
              tdClass = 'td_select'
              break
            case States.lock:
              tdClass = 'td_lock'
              break
          }
          return tdClass
        }
      },
      // 
      focusTd: {get () { return this.table.focusTd }},
      // 
      match: {get () { return this.table.matchType(this.column, this.data) }},
      // 
      value: {
        get () {
          let value = ''
          if (this.match) {
            switch (this.state) {
              // 
              case States.normal: {
                let text = this.column.text
                // 
                if (text instanceof Function) value = text(this.data, this.column)
                else {
                  if (this.data[this.column.key] !== undefined) value = this.data[this.column.key]
                  else value = this.table.nullString
                }
                break
              }
              case States.select: {
                if (this.data[this.column.key] !== undefined) value = this.data[this.column.key]
                break
              }
              case States.lock:
                if (this.data[this.column.key] !== undefined) value = this.data[this.column.key]
                break
            }
          }
          return value
        },
        set (value) { if (this.column.edit !== false && this.match) this.data[this.column.key] = value }
      }
    },
    methods: {
      handleInput () {
        if (!this.column.render) this.input = true
      },
      getState () {
        switch (this.state) {
          case States.normal:
            return 'normal'
          case States.lock:
            return 'lock'
          case States.select:
            return 'select'
        }
      },
      getThis () { return this },
      handleFocus () {
        console.log(this.value, '')
        if (this.focusTd) this.focusTd.state = States.normal
        this.table.focusTd = this
        this.input = false
        this.state = States.select
      },
      handleBlur () {
        this.state = States.normal
        if (!this.input) {
          if (this.debug) console.log(',')
        } else if (this.column.render) {
          if (this.debug) console.log(',')
        } else if (this.column.edit === false) {
          if (this.debug) console.log(this.column.label + ',')
        } else if (!this.match) {
          if (this.debug) console.log(this.data.pojo + '' + this.column.key + '(' + this.column.label + '),')
        } else if (!this.driver || !this.driver.updater || !this.driver.updater.hasOwnProperty(this.data.pojo)) {
          if (this.debug) console.log('')
        } else {
          let update = this.driver.updater[this.data.pojo]
          update(this.data, this.column)
        }
        this.input = false
      },
      handleEnter ($event) {
        if ($event.ctrlKey) {
          this.table.canMove = true
          this.table.down()
        }
      },
      handleDbClick () { this.state = States.lock },
      handleClick () {
        if (this.focusTd) this.focusTd.state = States.normal
        this.table.focusTd = this
        this.input = false
        this.state = States.select
        if (this.match && this.column.edit !== false) this.$refs.input.$el.focus()
      },
      blur () {
        this.state = States.normal
        if (this.column.render) this.state = States.normal
      },
      focus () {
        if (this.match && this.column.edit !== false && !this.column.render) this.$refs.input.$el.focus()
        else this.$refs.tdWarp.focus()
      }
    },
    data () {
      return {
        isMounted: false,
        state: States.normal,
        // 
        input: false
      }
    },
    mounted () {
      this.isMounted = true
      this.table.bindTd(this)
    },
    updated () { this.table.bindTd(this) }
  }
</script>
<style lang="scss" scoped>
    .td_normal {
        box-sizing: border-box;
        font-family: Arial, , serif;
        font-size: 11px;
    }

    .td_select {
        @extend .td_normal;
        border: 1px #66CC66 solid;
    }

    .td_lock {
        @extend .td_normal;
        border: 1px #3fcc2c solid;
    }

    .td_warp {
        outline: none;
        border: none;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        align-items: left;
        align-content: left;
    }
</style>



